<template>
    <div>
        <server-info v-if="currentStep === steps.findIndex(step=>step.key === 'serverInfo')"/>
        <identify-card-page v-if="currentStep === steps.findIndex(step=>step.key === 'identifyCardPage')"/>
        <face-yitu v-if="currentStep === steps.findIndex(step=>step.key === 'faceYitu')"/>
        <contrast v-if="currentStep === steps.findIndex(step=>step.key === 'contrast')"/>
        <process
            :type="1"
            v-if="currentStep === steps.findIndex(step=>step.key === 'process')"
            :handle-business="handleBusiness"
        />
        <typeChose v-if="currentStep === steps.findIndex(step=>step.key === 'typeChose')"/>
        <plateChose v-if="currentStep === steps.findIndex(step=>step.key === 'plateChose')"/>
        <check-phone v-if="currentStep === steps.findIndex(step=>step.key === 'checkPhone')"/>
        <choice-car v-if="currentStep === steps.findIndex(step=>step.key === 'choiceCar')"/>
        <record-list v-if="currentStep === steps.findIndex(step=>step.key === 'recordList')"/>
        <confirmation v-if="currentStep === steps.findIndex(step=>step.key === 'confirmation')"/>
        <pay-query
            v-if="currentStep === steps.findIndex(step=>step.key === 'payQuery')"
            :handle-complete="handleComplete"
        />
        <complete
            v-if="currentStep === steps.findIndex(step=>step.key === 'complete')"
            :data="illegalityResult"
            foot-texts="温馨提示：违法行为将在24小时之内消除，如消除失败，系统将于7个工作日之内自动退款"
        />
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import identifyCardPage from '@/view/public/before-work/identify-card-page';
import faceYitu from '@/view/public/before-work/face-yitu';
import contrast from '@/view/public/before-work/contrast';
import serverInfo from '@/components/server-info';
import recordList from './components/record-list';
import confirmation from './components/confirmation';
import choiceCar from './components/choice-car';
import payQuery from '@/components/pay-query';
import complete from '@/components/complete';
import process from '@/components/process';
import { illegalityElectronicStart, illegalityElectronicFace, illegalityElectronicComplute, checkOwnerCars } from '@/service/traffic/traffic';
import typeChose from './components/type-chose';
import plateChose from './components/plate-chose';
import checkPhone from './components/check-phone';

export default {
    name: 'illegality-electronic-suzhou',
    components: {
        identifyCardPage,
        faceYitu,
        contrast,
        serverInfo,
        recordList,
        confirmation,
        choiceCar,
        complete,
        payQuery,
        process,
        typeChose,
        plateChose,
        checkPhone
    },
    computed: {
        ...mapState('illegality', {
            illegalityResult: 'illegalityResult',
            illegalityElectronicSelectMonitor: 'illegalityElectronicSelectMonitor'
        }),
        ...mapState('common', {
            steps: 'steps',
            currentStep: 'currentStep',
            identifyCard: 'identifyCard',
            businessTypeCode: 'businessTypeCode',
            binocularCamera: 'binocularCamera'
        }),
        ...mapState('permanent', {
            BUSINESS_EN_NAME_CODE: 'BUSINESS_EN_NAME_CODE'
        })
    },
    methods: {
        ...mapMutations('common', ['setSteps', 'changeStep', 'changeTipPage']),
        ...mapMutations('illegality', ['setIllegalityResult', 'setCarLists']),
        async handleBusiness () {
            const res = await illegalityElectronicStart({
                idNum: this.identifyCard.certNumber
            });
            if (res && res.code === '0') {
                if (res.result.status === 1) {
                    this.handleContrast();
                } else {
                    this.changeTipPage({ message: res.message });
                }
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        },
        async handleContrast () {
            const res = await illegalityElectronicFace({
                identifiedPhoto: this.binocularCamera
            });
            if (res && res.code === '0') {
                this.checkOwnerCars();
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        },
        async checkOwnerCars () {
            const res = await checkOwnerCars({
                idNum: this.identifyCard.certNumber,
                businessType: this.businessTypeCode
            });
            if (res && res.code === '0') {
                this.setCarLists(res.result);
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        },
        async handleComplete () {
            const res = await illegalityElectronicComplute({
                payStatus: 1,
                monitorRecordIds: this.illegalityElectronicSelectMonitor.recordArr.map((item) => {
                    return item.monitorRecordId;
                })
            });
            if (res && res.code === '0') {
                this.setIllegalityResult(res.result);
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        }
    },
    mounted () {
        let data = [];

        // 查询业务无支付
        if (this.businessTypeCode === this.BUSINESS_EN_NAME_CODE.illegalityElectronic) {
            data = [
                {
                    name: '业务须知',
                    isActive: true,
                    visible: true,
                    key: 'serverInfo'
                },
                {
                    name: '身份识别',
                    isActive: false,
                    visible: true,
                    key: 'identifyCardPage'
                },
                {
                    name: '身份识别',
                    isActive: false,
                    visible: false,
                    key: 'faceYitu'
                },
                {
                    name: '身份识别',
                    isActive: false,
                    visible: false,
                    key: 'contrast'
                },
                {
                    name: '身份识别',
                    isActive: false,
                    visible: false,
                    key: 'process'
                },
                {
                    name: '机动车信息',
                    isActive: false,
                    visible: true,
                    key: 'typeChose'
                },
                {
                    name: '机动车信息',
                    isActive: false,
                    visible: false,
                    key: 'plateChose'
                },
                {
                    name: '机动车信息',
                    isActive: false,
                    visible: false,
                    key: 'checkPhone'
                },
                {
                    name: '机动车信息',
                    isActive: false,
                    visible: false,
                    key: 'choiceCar'
                },
                {
                    name: '监控记录',
                    isActive: false,
                    visible: true,
                    key: 'recordList'
                },
                {
                    name: '确认处罚信息',
                    isActive: false,
                    visible: true,
                    key: 'confirmation'
                },
                {
                    name: '进行支付',
                    isActive: false,
                    visible: true,
                    key: 'payQuery'
                },
                {
                    name: '完成支付',
                    isActive: false,
                    visible: true,
                    key: 'complete'
                }
            ];
        } else {
            data = [
                {
                    name: '身份识别',
                    isActive: true,
                    visible: true,
                    key: 'identifyCardPage'
                },
                {
                    name: '身份识别',
                    isActive: false,
                    visible: false,
                    key: 'faceYitu'
                },
                {
                    name: '身份识别',
                    isActive: false,
                    visible: false,
                    key: 'contrast'
                },
                {
                    name: '身份识别',
                    isActive: false,
                    visible: false,
                    key: 'process'
                },
                {
                    name: '机动车信息',
                    isActive: false,
                    visible: true,
                    key: 'typeChose'
                },
                {
                    name: '机动车信息',
                    isActive: false,
                    visible: false,
                    key: 'plateChose'
                },
                {
                    name: '机动车信息',
                    isActive: false,
                    visible: false,
                    key: 'checkPhone'
                },
                {
                    name: '机动车信息',
                    isActive: false,
                    visible: false,
                    key: 'choiceCar'
                },
                {
                    name: '监控记录',
                    isActive: false,
                    visible: true,
                    key: 'recordList'
                }
            ];
        }

        this.setSteps(data);
    }
};

</script>
