<template>
    <div>
        <div class="kd-page-title">
            输入机动车信息
        </div>
        <div class="kd-page-content-wrapper wrap-info">
            <car-input
                last-text="请输入车架编号后六位"
                :get-car-input="getCarInput"
            />
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button icon="kedaIcon-policework" text="返回首页" type-class="home"/>
            <kd-button icon="kedaIcon-circleleft" text="上一步" :button-handle="preStep"/>
            <kd-button icon="kedaIcon-circleright" text="下一步" :button-handle="nextStep" :but-disable="!(carLicense && carEngineCode)"/>
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import carInput from '@/components/car-input';
import { illegalityElectronicCheckMonitor } from '@/service/traffic/traffic';

export default {
    name: 'IllegalityElectronicChoiceCar',
    components: { carInput },
    computed: {
        ...mapState('illegality', {
            handleCarInfo: 'handleCarInfo'
        }),
        ...mapState('common', {
            businessTypeCode: 'businessTypeCode',
            identifyCard: 'identifyCard'
        }),
        ...mapState('permanent', {
            BUSINESS_EN_NAME_CODE: 'BUSINESS_EN_NAME_CODE'
        })
    },
    data () {
        return {
            carType: '02',
            carLicense: '',
            carEngineCode: '',
            list: []
        };
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        ...mapMutations('illegality', ['setIllegalityElectronicIntroduce', 'setIllegalityElectronicMonitorList', 'setHandleCarInfo']),
        getCarInput (data) {
            if (data.carType !== undefined) {
                this.carType = data.carType;
            }
            if (data.carLicense !== undefined) {
                this.carLicense = data.carLicense;
            }
            if (data.carEngineCode !== undefined) {
                this.carEngineCode = data.carEngineCode;
            }
        },
        preStep () {
            this.changeStep('typeChose');
        },
        async nextStep () {
            if (this.carLicense && this.carEngineCode) {
                this.setHandleCarInfo({
                    hphm: this.carLicense,
                    hpzl: this.carType,
                    clsbdh: this.carEngineCode
                });
                const res = await illegalityElectronicCheckMonitor({
                    plateType: this.handleCarInfo.hpzl,
                    plateNumber: this.handleCarInfo.hphm,
                    clsbdh: this.handleCarInfo.clsbdh
                });
                if (res && res.code === '0') {
                    if (res.result.code === 1) {
                        this.setIllegalityElectronicMonitorList(res.result.data);
                        this.changeStep('down');
                    } else {
                        this.changeTipPage({
                            message: res.message ? res.message : res.result.msg,
                            enableBack: true
                        });
                    }
                } else {
                    this.changeTipPage({
                        message: res.message ? res.message : res.result.msg,
                        enableBack: true
                    });
                }
            }
        }

    }

};
</script>

<style lang="scss" scoped>
    .wrap-info {
        .detail {
            cursor: pointer;
            color: #4494FF;
        }
    }

    .chooseItem {
        font-size: 20px;
        color: #5FAFEB;
        text-align: center;
    }

    .noChooseItem {
        font-size: 20px;
        color: #fff;
        text-align: center;
    }
</style>
