<template>
    <div>
        <div class="kd-page-title">
            请输入验证码
        </div>
        <div class="kd-page-content-wrapper wrap-info">
            <verify-code
                @getVerifyCode="getVerifyCode"
                :need-phone="false"
                :default-phone-number="handleCarInfo.phone"
                ref="verifyCode"
            />
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                icon="kedaIcon-circleleft"
                text="上一步"
                :button-handle="goUp"
            />
            <kd-button
                icon="kedaIcon-duihao2"
                text="提交"
                :button-handle="next"
            />
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import { getPhoneCode, getPhoneVerify } from '@/service/common';
import verifyCode from '@/components/verify-code';
import { illegalityElectronicCheckLocal } from '@/service/traffic/traffic';

export default {
    name: 'IllegalityElectronicCheckPhone',
    components: { verifyCode },
    data () {
        return {
            alertOn: false
        };
    },
    computed: {
        ...mapState('common', {
            identifyCard: 'identifyCard',
            businessTypeCode: 'businessTypeCode'
        }),
        ...mapState('illegality', {
            handleCarInfo: 'handleCarInfo'
        })
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        ...mapMutations('illegality', ['setIllegalityElectronicIntroduce']),
        async next () {
            if (this.$refs.verifyCode.submission()) {
                const res = await getPhoneVerify({
                    phone: this.handleCarInfo.phone,
                    code: this.$refs.verifyCode.item.verifyCode,
                    serviceType: this.businessTypeCode
                });
                if (res && res.code === '0') {
                    this.changeStep('down');
                } else {
                    this.changeTipPage({
                        message: res.message,
                        enableBack: true
                    });
                }
            }
        },
        async nextStep () {
            const res = await illegalityElectronicCheckLocal({
                plateType: this.handleCarInfo.hpzl,
                plateNumber: this.handleCarInfo.hphm,
                clsbdh: this.handleCarInfo.clsbdh
            });
            if (res && res.code === '0') {
                this.setIllegalityElectronicIntroduce(res.result.content);
                this.changeStep('down');
            } else {
                this.changeTipPage({
                    message: res.message,
                    imgName: 'imgDriverPerson',
                    enableBack: true
                });
            }
        },
        async getVerifyCode () {
            const res = await getPhoneCode({
                phone: this.handleCarInfo.phone,
                serviceType: this.businessTypeCode
            });
            if (!res || res.code !== '0') {
                this.changeTipPage({ message: res.message });
            }
        },
        goUp () {
            this.changeStep('up');
        }
    }
};
</script>

<style lang="scss" scoped>
    .wrap-info {
        display: flex;
        justify-content: center;
        align-items: center;
    }
</style>
