<template>
    <div>
        <div class="kd-page-title">
            选择车牌号
        </div>
        <div
            class="kd-page-content-wrapper wrap"
        >
            <div
                class="item"
                v-for="(item, index) in items"
                :key="index"
                @click="next(item.type)"
                :class="carLists.find((i)=>{return i.type === item.type}) || item.type === '2' ? '' : 'disableCar'"
            >
                <span class="kedaIconfont kedaIcon-cadIcon"/>
                <span>{{ item.name }}</span>
            </div>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import { cloneDeep } from 'lodash';
import audioCar from '@/assets/media/trafic/101.mp3';
import { playAudio } from '@/utils/media';

export default {
    name: 'IllegalityElectronicTypeChose',
    computed: {
        ...mapState('common', {
            identifyCard: 'identifyCard',
            businessTypeCode: 'businessTypeCode'
        }),
        ...mapState('illegality', {
            carLists: 'carLists'
        }),
        ...mapState('permanent', {
            BUSINESS_EN_NAME_CODE: 'BUSINESS_EN_NAME_CODE'
        })
    },
    mounted () {
        this.audioCar = playAudio(audioCar);
        this.audioCar.play();
    },
    beforeDestroy () {
        this.audioCar.destroy();
    },
    data () {
        return {
            items: [{
                type: '1',
                name: '我的机动车'
            }, {
                type: '0',
                name: '同户号机动车'
            }, {
                type: '2',
                name: '其他车辆'
            }]
        };
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage', 'setSteps']),
        ...mapMutations('illegality', ['setOwnCarLists', 'setCarLists']),
        next (key) {
            if (key === '2') {
                this.changeStep('choiceCar');
            } else {
                const list = cloneDeep(this.carLists);
                const filterList = list.filter((item) => {
                    return item.type === key;
                });
                if (filterList.length > 0) {
                    this.setOwnCarLists(filterList);
                    this.changeStep('down');
                }
            }
        }
    }

};
</script>

<style lang="scss" scoped>
    .wrap {
        display: flex;
        justify-content: space-evenly;
        align-items: center;

        ::v-deep .el-loading-mask {
            .el-loading-spinner i {
                font-size: 48px;
                color: #ffffff;
            }
        }

        .item {
            width: 200px;
            height: 220px;
            background: linear-gradient(180deg, rgba(137, 189, 242, 1) 0%, rgba(79, 153, 225, 1) 100%);
            border-radius: 10px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            overflow: hidden;
            border: 1PX solid #ffffff;/*no*/

            span {
                color: #ffffff;
                font-size: 19.2px;
            }

            span:first-child {
                font-size: 48px;
                padding-bottom: 13px;
            }
        }

        .disableCar {
            background: linear-gradient(180deg, rgba(197, 215, 234, 1) 0%, rgba(165, 191, 216, 1) 100%);
        }
    }
</style>
