<template>
    <div>
        <div class="kd-page-title">
            选择车牌号
        </div>
        <div class="kd-page-content-wrapper wrap">
            <div class="label">
                {{ ownCarLists.length > 0 && ownCarLists[0].type === '1' ? '我拥有的机动车：' : '同户号机动车：' }}
            </div>
            <div class="content">
                <div
                    class="item"
                    v-for="(item,index) in ownCarLists"
                    @click="next(item)"
                    :key="index"
                >
                    <span class="kedaIconfont kedaIcon-yuan"/>
                    <span>{{ CONSTANT.carTypeName }}{{ item.hphm }}</span>
                </div>
            </div>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                icon="kedaIcon-circleleft"
                text="上一步"
                :button-handle="preStep"
            />
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import { illegalityElectronicCheckMonitor } from '@/service/traffic/traffic';
import audioCar from '@/assets/media/trafic/102.mp3';
import { playAudio } from '@/utils/media';

export default {
    name: 'IllegalityElectronicPlateChose',
    computed: {
        ...mapState('illegality', {
            ownCarLists: 'ownCarLists',
            handleCarInfo: 'handleCarInfo'
        })
    },
    mounted () {
        this.audioCar = playAudio(audioCar);
        this.audioCar.play();
    },
    beforeDestroy () {
        this.audioCar.destroy();
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage', 'setSteps']),
        ...mapMutations('illegality', ['setHandleCarInfo', 'setIllegalityElectronicMonitorList']),
        preStep () {
            this.changeStep('up');
        },
        next (info) {
            this.setHandleCarInfo(info);
            if (info.type === '1') {
                this.nextStep();
            } else {
                this.changeStep('checkPhone');
            }
        },
        async nextStep () {
            const res = await illegalityElectronicCheckMonitor({
                plateType: this.handleCarInfo.hpzl,
                plateNumber: this.handleCarInfo.hphm,
                clsbdh: this.handleCarInfo.clsbdh
            });
            if (res && res.code === '0') {
                if (res.result.code === 1) {
                    this.setIllegalityElectronicMonitorList(res.result.data);
                    this.changeStep('recordList');
                } else {
                    this.changeTipPage({
                        message: res.message ? res.message : res.result.msg,
                        enableBack: true
                    });
                }
            } else {
                this.changeTipPage({
                    message: res.message ? res.message : res.result.msg,
                    enableBack: true
                });
            }
        }
    }
};
</script>

<style lang="scss" scoped>
    .wrap {
        display: flex;
        align-items: baseline;

        .label {
            width: 20%;
            line-height: 10vw;
            text-align: right;
            font-size: 19.2px;
        }

        .content {
            width: 80%;
            display: inline-flex;
            flex-wrap: wrap;
        }

        .item {
            width: 18vw;
            height: 6vw;
            margin: 0 10px 20px;
            background: linear-gradient(180deg, rgba(137, 189, 242, 1) 0%, rgba(79, 153, 225, 1) 100%);
            border-radius: 8px;
            border: 1PX solid #ffffff;/*no*/
            display: flex;
            justify-content: center;
            align-items: center;

            span:first-child {
                font-size: 22.4px;
                color: #ffffff;
                padding-right: 10px;
            }

            span:last-child {
                font-size: 19.2px;
                color: #ffffff;
            }
        }
    }
</style>
