<template>
    <div>
        <div class="kd-page-title">
            交通违法驾驶人确认书
        </div>
        <div class="kd-page-content-wrapper kd-page-padding wrap-info">
            <p>经与交通技术监控设备采集的违法信息进行核查对比</p>
            <p>本人（姓名：{{ illegalityElectronicSelectMonitor.holder }} 联系电话：{{ illegalityElectronicSelectMonitor.mobile }} 驾驶证号：{{ illegalityElectronicSelectMonitor.licenceNo }}） 承认驾驶车牌号为{{ carTypeName }}{{ illegalityElectronicSelectMonitor.plateNumber }}的小型汽车在以下时间，地点实施交通违法行为：</p>
            <div class="kd-table-style">
                <el-table
                    :data="illegalityElectronicSelectMonitor.recordArr"
                    style="width: 100%"
                >
                    <el-table-column
                        width="250"
                        label="违法时间"
                    >
                        <template slot-scope="scope">
                            {{ $momentFormat(scope.row.illegalTime) }}
                        </template>
                    </el-table-column>
                    <el-table-column
                        label="违法地点"
                        prop="illegalPlace"
                        show-overflow-tooltip
                    />
                    <el-table-column
                        label="违法行为"
                        prop="wfnr"
                        show-overflow-tooltip
                    />
                    <el-table-column
                        label="记分值"
                        prop="score"
                        width="120"
                    />
                    <el-table-column
                        label="罚款金额"
                        prop="fines"
                        width="120"
                    />
                </el-table>
                <div class="kd-total-content">
                    <div class="left"/>
                    <div class="right">
                        <p>共计<span>{{ illegalityElectronicSelectMonitor.totalScore }}</span>分</p>
                        <p>罚款<span>{{ illegalityElectronicSelectMonitor.totalFines }}</span>元</p>
                    </div>
                </div>
            </div>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                icon="kedaIcon-circleleft"
                text="上一步"
                :button-handle="goUp"
            />
            <kd-button
                icon="kedaIcon-circleright"
                text="接受处罚"
                :button-handle="nextStep"
            />
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import { illegalityElectronicAccept } from '@/service/traffic/traffic';
import { cloneDeep } from 'lodash';

export default {
    name: 'IllegalityElectronicConfirmation',
    data () {
        return {
            carTypeName: this.CONSTANT.carTypeName
        };
    },
    computed: {
        ...mapState('illegality', {
            illegalityElectronicSelectMonitor: 'illegalityElectronicSelectMonitor'
        })
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        ...mapMutations('illegality', ['setIllegalityPayUrl']),
        async nextStep () {
            const data = cloneDeep(this.illegalityElectronicSelectMonitor);
            delete data.recordArr;
            const res = await illegalityElectronicAccept({
                ...data,
                jkbhs: this.illegalityElectronicSelectMonitor.recordArr.map((item) => {
                    return item.jkbh;
                })
            });
            if (res && res.code === '0') {
                this.setIllegalityPayUrl(res.result);
                this.changeStep('down');
            } else {
                this.changeTipPage({
                    message: res.message,
                    enableBack: true
                });
            }
        },
        goUp () {
            this.changeStep('up');
        }
    }

};
</script>

<style lang="scss" scoped>
    .wrap-info {
        display: flex;
        flex-direction: column;

        > p {
            padding-bottom: 10px;
        }

        .total-content {
            display: flex;
            justify-content: space-between;
            padding: 15px;
            color: rgba(60, 75, 120, 1);

            .change-page {
                color: rgba(142, 190, 252, 1);
                font-size: 26px;

                span {
                    cursor: pointer;
                    margin-right: 25px;
                }
            }

            .total-info {
                display: flex;
                font-size: 26px;

                p {
                    margin-right: 20px;
                }

                span {
                    color: #FE5701;
                }
            }
        }
    }
</style>
