<template>
    <div>
        <div class="kd-page-title">
            机动车违法信息查询结果
        </div>
        <div class="kd-page-content-wrapper wrap-info kd-table-style">
            <el-table
                :data="pageData"
                ref="multipleTable"
                header-row-class-name="header-row"
                style="width: 100%"
                :highlight-current-row="true"
                @selection-change="selectionChange"
            >
                <el-table-column
                    v-if="businessTypeCode === BUSINESS_EN_NAME_CODE.illegalityElectronic"
                    type="selection"
                    width="50"
                />
                <el-table-column
                    width="140"
                    label="违法时间"
                >
                    <template slot-scope="scope">
                        {{ $momentFormat(scope.row.illegalTime) }}
                    </template>
                </el-table-column>

                <el-table-column
                    prop="illegalPlace"
                    label="违法地点"
                    show-overflow-tooltip
                />

                <el-table-column
                    prop="wfnr"
                    label="违法行为"
                    show-overflow-tooltip
                />

                <el-table-column
                    prop="score"
                    label="记分值"
                    width="100"
                />

                <el-table-column
                    width="150"
                    label="罚款金额"
                >
                    <template slot-scope="scope">
            <span>
              {{ '￥'+scope.row.fines }}
            </span>
                    </template>
                </el-table-column>
            </el-table>
            <!-- 总费用： -->
            <div class="kd-total-content">
                <div class="left">
                    <span @click="changePage(-1)"><i class="el-icon-caret-left"/>上一页</span>
                    <span @click="changePage(1)">下一页<i class="el-icon-caret-right"/></span>
                </div>
                <div
                    class="right"
                    v-if="businessTypeCode === BUSINESS_EN_NAME_CODE.illegalityElectronic"
                >
                    <p style="margin-right: 13px;">
                        共计
                    </p>
                    <p>
                        扣分<span
                        style="margin-right: 13px;"
                        class="total-info"
                    >{{ selection.length > 0 ? totalScore : 0 }}</span>
                    </p>
                    <p>处理罚款<span class="total-info">{{ selection.length > 0 ? totalFines : 0 }}</span>元</p>
                </div>
            </div>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                icon="kedaIcon-circleleft"
                text="上一步"
                :button-handle="preStep"
            />
            <kd-button
                icon="kedaIcon-circleright"
                v-if="nextIsExit"
                text="下一步"
                :button-handle="checkQualifications"
                :but-disable="selection.length === 0 "
            />
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import { illegalityElectronicSaveMonitorList, checkQualifications } from '@/service/traffic/traffic';
import audioCar from '@/assets/media/trafic/9.mp3';
import { playAudio } from '@/utils/media';

export default {
    name: 'IllegalityElectronicRecordList',
    data () {
        return {
            nextIsExit: false,
            detailBol: false,
            selection: [],
            currentPage: 1,
            defaultNum: 5, // 默认显示三条数据
            pageData: [],
            isChangePage: false// 翻页的时候，回触发变空
        };
    },
    computed: {
        ...mapState('illegality', {
            illegalityElectronicMonitorList: 'illegalityElectronicMonitorList',
            illegalityElectronicSelectMonitor: 'illegalityElectronicSelectMonitor',
            handleCarInfo: 'handleCarInfo'
        }),
        ...mapState('permanent', {
            BUSINESS_EN_NAME_CODE: 'BUSINESS_EN_NAME_CODE'
        }),
        ...mapState('common', {
            businessTypeCode: 'businessTypeCode',
            identifyCard: 'identifyCard'
        }),
        totalPage () {
            return Math.ceil(this.illegalityElectronicMonitorList.length / this.defaultNum) || 1;
        },
        totalScore () {
            let total = 0;
            this.selection.forEach((item) => {
                total += item.score;
            });
            return total;
        },
        totalFines () {
            let total = 0;
            this.selection.forEach((item) => {
                total += item.fines;
            });
            return total;
        }
    },
    mounted () {
        this.audioCar = playAudio(audioCar);
        this.changePage();
        this.isChangePage = false;

        // 查询情况不显示下一步
        if (this.businessTypeCode === this.BUSINESS_EN_NAME_CODE.illegalityElectronic) {
            this.nextIsExit = true;
            this.audioCar.play();
        }
    },
    beforeDestroy () {
        this.audioCar.destroy();
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        ...mapMutations('illegality', ['setIllegalityElectronicSelectMonitor']),
        closeDetail () {
            this.detailBol = false;
        },
        preStep () { // 上一步
            this.changeStep('typeChose');
        },
        async checkQualifications () {
            const res = await checkQualifications({
                name: this.identifyCard.partyName,
                idNum: this.identifyCard.certNumber,
                plateType: this.handleCarInfo.hpzl,
                plateNumber: this.handleCarInfo.hphm,
                monitorRecordIds: this.selection.map((item) => {
                    return item.monitorRecordId;
                }),
                bhList: this.selection.map((item) => {
                    return item.jkbh;
                })
            });
            if (res && res.code === '0') {
                await this.nextStep();
            } else {
                this.changeTipPage({
                    message: res.message ? res.message : res.result.msg,
                    enableBack: true
                });
            }
        },
        async nextStep () {
            const res = await illegalityElectronicSaveMonitorList({
                ...this.selection[0],
                monitorRecordIds: this.selection.map((item) => {
                    return item.monitorRecordId;
                })
            });
            if (res && res.code === '0') {
                this.setIllegalityElectronicSelectMonitor({
                    list: res.result,
                    totalFines: this.totalFines,
                    totalScore: this.totalScore
                });
                this.changeStep('down');
            } else {
                this.changeTipPage({
                    message: res.message,
                    enableBack: true
                });
            }
        },
        changePage (type) {
            let page = this.currentPage;
            if (type === -1) {
                page = page - 1;
                if (page < 1) {
                    return;
                }
            } else if (type === 1) {
                page = page + 1;
                if (page > this.totalPage) {
                    return;
                }
            }
            this.currentPage = page;
            this.pageData = this.illegalityElectronicMonitorList ? this.illegalityElectronicMonitorList.slice(this.defaultNum * (page - 1), this.defaultNum * (page - 1) + this.defaultNum) : [];

            // 保留已选择的数据
            this.isChangePage = true;
            setTimeout(() => {
                this.selection.forEach((row) => {
                    this.$refs.multipleTable.toggleRowSelection(row);
                });
            });
        },
        selectionChange (selection) {
            if (this.isChangePage) {
                this.selection = this.selection.concat(selection);
            } else {
                this.selection = selection;
            }
            this.isChangePage = false;
        }
    }

};
</script>

<style lang="scss" scoped>

    .wrap-info {
        .detail {
            cursor: pointer;
            color: #4494FF;
        }

        ::v-deep .el-checkbox__inner {
            width: 16px;
            height: 16px;
        }

        ::v-deep .el-checkbox__input.is-checked .el-checkbox__inner, .el-checkbox__input.is-indeterminate .el-checkbox__inner {
            background-color: #5FAFEB;
            border-color: #5FAFEB;
        }
    }

    .wrap-info + .waning-tip {
        width: 84.37vw;
        color: rgba(60, 75, 120, 1);
        font-size: 16px;
        margin: 8px auto;
    }

</style>
